@use '../base/variables' as v;
@use '../base/mixins' as m;


.filtros {
    input{
        display: block;
        text-align: center;
        width: fit-content;
        margin: 0 auto;
    }
   
    padding: 1rem;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    background-color: v.$grisoscuro;
    color: v.$primario;
    margin-bottom: 2rem;

    .inputGaleria {
        padding: 1rem;
        border: none;
        background-color: v.$grisoscuro;
        color: v.$primario;
        font-weight: v.$bold;

        &:hover {
            cursor: pointer;
            background-color: lighten($color: v.$grisoscuro, $amount: 15%);
        }

    }

    .selected-inputGaleria {
        @extend .inputGaleria;
        background-color: lighten($color: v.$grisoscuro, $amount: 15%);
    }
}

@include m.desktop {
    .filtros {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;

        li {

            &:hover {
                cursor: pointer;
                background-color: lighten($color: v.$grisoscuro, $amount: 15%);
            }
        }
    }
}

.galeria {
    display: block;
    width: 100%;
    height: fit-content;
    margin: 0 auto;
    // img{
    //     margin: 1rem;
    //     display: block;
    //     width: 100%;
    //     height: auto;
    // }
   
    .cadaimagen {
        position: relative;
        height: 100%;
        max-height:fit-content ;
        margin-bottom: 2rem;
        width: 100%;
       
        @supports(object-fit: contain) {
            img {
                height: 100%;
                width: 100%;
                object-fit: contain;
                // object-position: bottom center;
            }
        }
        .botoneliminar {
            position:absolute;
            top: 10px;
            right: 10px;
            z-index: 2;
        }
    }
   

    @include m.desktop {
       display: flex;
       justify-content: space-around;
       align-items: center;
       gap: 2rem;
       flex-wrap: wrap;
      .cadaimagen{
        flex-basis:calc(50% - 1rem);
        margin-bottom: 0;
      }
    }



}