@use '../base/variables' as v;
@use '../base/mixins' as m;

.header {
    background-image: url('/static/img/bg.jpg');
    height: 100vh;
    width: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    .inicio{
      
        
        .logo{

            background-image: url('/static/img/bore.png');
            height: 50vh;
            width: 100%;
            margin: 0 auto;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;   
           
        }
        .ubicacion {
            margin: 0;
            padding: 0;
            display: flex;
            height: 80vh;
            flex-direction: row;
           align-items: baseline;
            justify-content: center;
           
            p{
                font-size: 1.4rem;
                color: v.$gris;
                @include m.desktop{
                    font-size: 2.2rem;
                }
                
            }
            svg{
                width: 2rem;
                @include m.desktop{
                    width: 5rem;
                }
              
            }
          
        }
    }
    

}