@use '../base/mixins' as m;
@use '../base/variables' as v;


.trabajos {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
  h3{
    margin: 0;
  }
  
}
.trabajos::after {
    display: none;
}
@include m.desktop{
    .contenedor-trabajos {
        display: flex;
        justify-content: space-between;
    }
    .trabajos {
        flex-basis: calc(33.3% - 1rem);
        display: flex;
        padding: 2rem 1rem;
      h3{
        margin: 0;
      }
      
    }
   
}
.trabajos-titulo {
    color: v.$grisoscuro;
    text-align: center;
    font-size: 2.5rem;
}


.trabajos-contenedorTexto--texto {
    text-align: center;
    font-size: 1.4rem;
}
