@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
}
*, 
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.5;
    &.fijar-body {
        overflow: hidden;
       
    }
    &.body-scroll {
        padding-top: 8.6rem;
    }
}

p {
    font-size: 2rem;
    line-height: 1.5;
}

a{
    text-decoration: none;
    color: v.$grisoscuro;
    font-weight: v.$bold;
    &:hover{
        cursor: pointer;
    }
}  
h1, h2, h3 {
    margin: 0 0 2rem 0;
    line-height: 1.5;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
    margin: 2rem auto;
}
h3 {
    font-size: 3rem;
}
ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}