@use '../base/variables' as v;

.new {
    width: 100%;
      
      .top,
      .bottom {
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        padding: 5px;
        margin: 20px;
        display: flex;
  
        h1 {
          color: v.$grisoscuro;
          font-size: 20px;
        }
  
        .left {
          flex: 1;
          text-align: center;
  
          img {
            width: 300px;
            height: 300px;
            object-fit: cover;
          }
        }
  
        .right {
          flex: 2;
  
          form {
            display: flex;
            flex-wrap: wrap;
            gap: 30px;
            justify-content: space-around;
  
            .formInput {
              width: 40%;
  
              label {
                display: flex;
                align-items: center;
                gap: 10px;
  
                .icon {
                  cursor: pointer;
                }
              }
  
              input {
                width: 100%;
                padding: 5px;
                border: none;
                border-bottom: 1px solid gray;
              }
            }
  
            button {
              cursor: pointer;
              margin-top: 10px;

              &:disabled{
                background-color: rgba(92, 64, 3, 0.322);
                cursor: not-allowed;
              }
            }
          }
        }
    }
  }