@use  '../base/variables' as v;


.footer{
    background-color: v.$grisoscuro;
    margin-top: 2rem;
    color: v.$gris;
    text-align: center;
    display: flex;
    flex-direction: column;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    .contRedes{
        display: flex;
        align-items: center;
        .redes{
            max-width: 600px;
    
            margin: 2rem 0;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }
 
}