html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

body.fijar-body {
  overflow: hidden;
}

body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: #3d3d3d;
  font-weight: 700;
}

a:hover {
  cursor: pointer;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  margin: 2rem auto;
}

h3 {
  font-size: 3rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.contenedor {
  width: 95%;
  padding: 5rem 0;
  margin: 3rem auto;
}

@media (min-width: 768px) {
  .contenedor {
    max-width: 1200px;
  }
}
h1, h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  text-align: center;
}

h2::after,
.trabajos::after {
  content: url("/static/img/puntitos.png");
  display: flex;
  flex-direction: column;
  align-items: center;
}

h3 {
  font-family: "Roboto", sans-serif;
}

.sombra {
  -webkit-box-shadow: 0px 5px 15px -3px rgba(120, 120, 120, 0.58);
  -moz-box-shadow: 0px 5px 15px -3px rgba(120, 120, 120, 0.58);
  box-shadow: 0px 5px 15px -3px rgba(120, 120, 120, 0.58);
  padding: 2rem;
  border-radius: 1rem;
}

.margin-2rem {
  margin: 2rem auto;
}

.visto {
  background-color: #e1e1e1;
}

.no-visto {
  border: solid 2px rgba(253, 186, 2, 0.9921568627);
}

.selected {
  background-color: #636363;
}

.overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.651);
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  padding: 3rem;
  place-items: center;
  z-index: 999;
}

.overlay .contImg {
  height: 90%;
  max-height: 600px;
  width: 100%;
  z-index: 999;
}

@supports (object-fit: contain) {
  .overlay .contImg img {
    display: block;
    margin: 0 auto;
    width: auto;
    height: 100%;
    object-fit: cover;
    border: 3px solid #e1e1e1;
  }
}
.none {
  display: none;
}

.boton-principal {
  background-color: #3d3d3d;
  color: rgba(253, 186, 2, 0.9921568627);
  font-weight: 700;
  width: 100%;
  text-decoration: none;
  padding: 0.8rem 2rem;
  text-align: center;
  display: block;
  border: none;
  text-transform: uppercase;
  margin-right: 1rem;
  white-space: nowrap;
}

.boton-principal:last-of-type {
  margin-right: 0;
}

.boton-principal:hover {
  background-color: #303030;
  cursor: pointer;
}

.boton-secundario {
  background-color: rgba(253, 186, 2, 0.9921568627);
  color: #3d3d3d;
  font-weight: 700;
  width: 100%;
  text-decoration: none;
  padding: 0.8rem 2rem;
  text-align: center;
  display: block;
  border: none;
  text-transform: uppercase;
  margin-right: 1rem;
  white-space: nowrap;
  width: fit-content;
}

.boton-secundario:last-of-type {
  margin-right: 0;
}

.boton-secundario:hover {
  background-color: rgba(228, 167, 2, 0.9921568627);
  cursor: pointer;
}

.boton-eliminar {
  background-color: #c53838;
  color: #e1e1e1;
  font-weight: 700;
  width: 100%;
  text-decoration: none;
  padding: 0.8rem 2rem;
  text-align: center;
  display: block;
  border: none;
  text-transform: uppercase;
  margin-right: 1rem;
  white-space: nowrap;
  width: fit-content;
}

.boton-eliminar:last-of-type {
  margin-right: 0;
}

.boton-eliminar:hover {
  background-color: #b13232;
  cursor: pointer;
}

.errores {
  background-color: #c53838;
  padding: 1rem;
  font-weight: 700;
  font-size: 1.4rem;
  color: #FFF;
  text-align: center;
  text-transform: uppercase;
  margin: 2rem 0;
}

.error {
  border: solid 1px #c53838;
}

/*  UI */
.header {
  background-image: url("/static/img/bg.jpg");
  height: 100vh;
  width: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header .inicio .logo {
  background-image: url("/static/img/bore.png");
  height: 50vh;
  width: 100%;
  margin: 0 auto;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.header .inicio .ubicacion {
  margin: 0;
  padding: 0;
  display: flex;
  height: 80vh;
  flex-direction: row;
  align-items: baseline;
  justify-content: center;
}

.header .inicio .ubicacion p {
  font-size: 1.4rem;
  color: #e1e1e1;
}

@media (min-width: 768px) {
  .header .inicio .ubicacion p {
    font-size: 2.2rem;
  }
}
.header .inicio .ubicacion svg {
  width: 2rem;
}

@media (min-width: 768px) {
  .header .inicio .ubicacion svg {
    width: 5rem;
  }
}
.navbar {
  max-width: 100%;
  background-color: #3d3d3d;
}

.navbar .cont-nav {
  width: 100%;
}

.navbar .cont-nav .logoNav {
  background-image: url("/static/img/bore.png");
  height: 70px;
  width: 100%;
  flex: 0 1 20rem;
  margin: 0 auto;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.navbar .cont-nav .enlacesNav {
  display: block;
  margin: 0 auto;
  text-align: center;
  color: #FFF;
  font-size: 1.8rem;
}

.navbar .cont-nav .enlacesNav li {
  padding: 1rem;
  border-radius: 0.5rem;
}

.navbar .cont-nav .enlacesNav li:hover {
  background-color: rgba(253, 186, 2, 0.9921568627);
  cursor: pointer;
  transition: background-color 0.5s linear;
}

.LinkAdmin::after {
  background-image: url("/static/img/perfil.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  display: inline-block;
  width: 20px;
  height: 20px;
  content: "";
}

@media (min-width: 768px) {
  .navbar .cont-nav {
    display: flex;
    align-items: center;
    align-content: flex-start;
    justify-content: left;
    max-width: 1200px;
    height: 80px;
    margin: 0 auto;
  }
  .navbar .cont-nav .enlacesNav {
    display: flex;
    justify-content: center;
    color: #FFF;
    gap: 2rem;
    font-size: 1.8rem;
    flex: 0.8;
  }
  .navbar .cont-nav .enlacesNav li {
    padding: 1rem;
    border-radius: 0.5rem;
  }
  .navbar .cont-nav .enlacesNav li:hover {
    background-color: rgba(253, 186, 2, 0.9921568627);
    cursor: pointer;
    transition: background-color 0.5s linear;
  }
}
.formulario-admin {
  width: 95%;
  margin: 2rem auto 2rem auto;
}

.formulario-admin .campo {
  margin-bottom: 2rem;
  display: block;
  width: 95%;
}

.formulario-admin .campo label {
  font-size: 1.8rem;
}

.formulario-admin .campo input {
  display: block;
  width: 100%;
  padding: 1rem;
}

.formulario-admin .campo textarea {
  width: 100%;
  padding: 1rem;
  height: 150px;
}

.formulario-admin .campo select {
  display: block;
  width: 100%;
  padding: 1rem;
}

@media (min-width: 768px) {
  .formulario-admin {
    max-width: 60rem;
  }
  .formulario-admin .campo {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  .formulario-admin .campo label {
    flex: 0 0 15rem;
    font-size: 1.8rem;
  }
  .formulario-admin .campo input {
    flex: 1;
  }
  .formulario-admin .campo textarea {
    flex: 1;
  }
  .formulario-admin .campo select {
    flex: 1;
  }
}
.trabajos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.trabajos h3 {
  margin: 0;
}

.trabajos::after {
  display: none;
}

@media (min-width: 768px) {
  .contenedor-trabajos {
    display: flex;
    justify-content: space-between;
  }
  .trabajos {
    flex-basis: calc(33.3% - 1rem);
    display: flex;
    padding: 2rem 1rem;
  }
  .trabajos h3 {
    margin: 0;
  }
}
.trabajos-titulo {
  color: #3d3d3d;
  text-align: center;
  font-size: 2.5rem;
}

.trabajos-contenedorTexto--texto {
  text-align: center;
  font-size: 1.4rem;
}

.info {
  color: #3d3d3d;
}

.info h3 {
  text-align: center;
}

.info .imagen-info {
  flex-direction: column;
}

.info .imagen-info img {
  border-radius: 1rem;
  max-width: 10rem;
}

.info-ema span {
  font-size: 1.8rem;
  color: #3d3d3d;
  font-weight: 700;
}

.img__ema {
  width: 23rem;
  height: 100%;
}

@media (min-width: 768px) {
  .info .imagen-info {
    padding: 2rem;
    flex-direction: row;
    align-items: center;
  }
  .info .imagen-info img {
    max-width: 25rem;
    /* filter: grayscale(25);*/
  }
  .info .info-ema {
    padding: 2rem;
    flex-direction: column;
  }
}
.contacto {
  display: flex;
  flex-direction: column;
  align-content: center;
}

.contacto .contButton {
  width: fit-content;
  display: block;
  margin: 0 auto;
}

.footer {
  background-color: #3d3d3d;
  margin-top: 2rem;
  color: #e1e1e1;
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
}

.footer .contRedes {
  display: flex;
  align-items: center;
}

.footer .contRedes .redes {
  max-width: 600px;
  margin: 2rem 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.seccionImagen {
  width: 100%;
  height: 50rem;
  background-image: url("/static/img/ce-7.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #e1e1e1;
}

.seccionImagen .titulo-seccion {
  text-align: center;
}

.contenedor-mensaje .detalle-mensjae {
  border: solid 1px #3d3d3d;
  border-radius: 0.5rem;
  padding: 2rem;
}

.contenedor-mensaje .infoContacto {
  display: block;
}

@media (min-width: 768px) {
  .contenedor-mensaje .infoContacto {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    gap: 2rem;
  }
}
.contenedor-mensaje .infoContacto span {
  font-weight: 700;
  display: block;
}

.new {
  width: 100%;
}

.new .top,
.new .bottom {
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 5px;
  margin: 20px;
  display: flex;
}

.new .top h1,
.new .bottom h1 {
  color: #3d3d3d;
  font-size: 20px;
}

.new .top .left,
.new .bottom .left {
  flex: 1;
  text-align: center;
}

.new .top .left img,
.new .bottom .left img {
  width: 300px;
  height: 300px;
  object-fit: cover;
}

.new .top .right,
.new .bottom .right {
  flex: 2;
}

.new .top .right form,
.new .bottom .right form {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-around;
}

.new .top .right form .formInput,
.new .bottom .right form .formInput {
  width: 40%;
}

.new .top .right form .formInput label,
.new .bottom .right form .formInput label {
  display: flex;
  align-items: center;
  gap: 10px;
}

.new .top .right form .formInput label .icon,
.new .bottom .right form .formInput label .icon {
  cursor: pointer;
}

.new .top .right form .formInput input,
.new .bottom .right form .formInput input {
  width: 100%;
  padding: 5px;
  border: none;
  border-bottom: 1px solid gray;
}

.new .top .right form button,
.new .bottom .right form button {
  cursor: pointer;
  margin-top: 10px;
}

.new .top .right form button:disabled,
.new .bottom .right form button:disabled {
  background-color: rgba(92, 64, 3, 0.322);
  cursor: not-allowed;
}

.allmensajes {
  max-width: 800px;
  max-height: 800px;
  overflow-y: scroll;
  scroll-behavior: smooth;
  margin: 3rem auto;
}

.contenedorMensaje {
  background-color: #e1e1e1;
  margin: 2rem auto;
}

.contenedorMensaje .nombre {
  color: #3d3d3d;
  font-weight: 700;
}

.contenedorMensaje .nombre:hover {
  cursor: pointer;
  color: rgba(202, 149, 2, 0.9921568627);
}

.tituloError {
  padding: 5rem;
}

.tituloError h1::before {
  background-image: url("/static/img/alert.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  display: inline-block;
  width: 80px;
  height: 50px;
  content: "";
}

.filtros {
  padding: 1rem;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  background-color: #3d3d3d;
  color: rgba(253, 186, 2, 0.9921568627);
  margin-bottom: 2rem;
}

.filtros input {
  display: block;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
}

.filtros .inputGaleria, .filtros .selected-inputGaleria {
  padding: 1rem;
  border: none;
  background-color: #3d3d3d;
  color: rgba(253, 186, 2, 0.9921568627);
  font-weight: 700;
}

.filtros .inputGaleria:hover, .filtros .selected-inputGaleria:hover {
  cursor: pointer;
  background-color: #636363;
}

.filtros .selected-inputGaleria {
  background-color: #636363;
}

@media (min-width: 768px) {
  .filtros {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  .filtros li:hover {
    cursor: pointer;
    background-color: #636363;
  }
}
.galeria {
  display: block;
  width: 100%;
  height: fit-content;
  margin: 0 auto;
}

.galeria .cadaimagen {
  position: relative;
  height: 100%;
  max-height: fit-content;
  margin-bottom: 2rem;
  width: 100%;
}

@supports (object-fit: contain) {
  .galeria .cadaimagen img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
}
.galeria .cadaimagen .botoneliminar {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 2;
}

@media (min-width: 768px) {
  .galeria {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
  }
  .galeria .cadaimagen {
    flex-basis: calc(50% - 1rem);
    margin-bottom: 0;
  }
}
.cargando {
  margin: 2rem auto;
  max-width: fit-content;
}

.cargando .cargandobarra {
  border-bottom: 5px solid rgba(253, 186, 2, 0.9921568627);
  overflow: hidden;
  white-space: nowrap;
  animation: progres 3s linear;
}

.cargando h1 {
  text-align: center;
  border-right: 3px solid;
  width: fit-content;
  white-space: nowrap;
  overflow: hidden;
  margin: 0 auto;
  animation: typing 2s steps(16), blink 0.5s step-end infinite;
}

@keyframes progres {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
@keyframes typing {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}
.bloques {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  align-items: center;
}

.bloques .bloque {
  width: 95%;
  margin: 0 auto;
  text-align: center;
}

.bloques .bloque h3 {
  margin: 0;
  font-size: 2rem;
}

.bloques .bloque p {
  font-size: 1.4rem;
}