@use '../base/variables' as v;
@use '../base/mixins' as m;

.formulario-admin {

    width: 95%;
    margin: 2rem auto 2rem auto;

    .campo {
        margin-bottom: 2rem;
        display: block;
        width: 95%;
        
        label {
            
            font-size: 1.8rem;
        }

        input {
            display: block;
            width: 100%;
            padding: 1rem;
        }

        textarea {
            width: 100%;
            padding: 1rem;
            height: 150px;
        }

        select {
            display: block;
            width: 100%;
            padding: 1rem;
        }
    }


    @include m.desktop {
        max-width: 60rem;

        .campo {
            display: flex;
            align-items: center;
            gap: 1rem;
            label {
                flex: 0 0 15rem;
                font-size: 1.8rem;
            }
            input {
                flex: 1;
            }

            textarea {
                flex: 1;
            }

            select {
                flex: 1;
            }
        }
    }

}