@use "variables" as v;

@mixin telefono {
  @media (min-width: v.$telefono) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}

@mixin desktopxl {
  @media (min-width: v.$desktopxl) {
    @content;
  }
}

@mixin grid($columnas, $espaciado) {
  display: grid;
  grid-template-columns: repeat($columnas, 1fr);
  gap: $espaciado;
  align-items: center;
}
@mixin mediaGrid($ancho, $espaciado) {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax($ancho, 1fr));
  gap: $espaciado;
  align-items: center;
}


@mixin boton($bgColor,$color, $display) {
    background-color: $bgColor;
    color: $color;
    font-weight: v.$bold;
    width: 100%;
    text-decoration: none;
    padding: 0.8rem 2rem;
    text-align: center;
    display: $display;
    border: none;
    text-transform: uppercase;
    margin-right: 1rem;
    &:last-of-type{
      margin-right: 0;
    }
    &:hover{
        background-color: darken($color: $bgColor, $amount: 5);
        cursor: pointer;
    }
}

@mixin borderRadius {
  border-radius: 0.5rem;
}
