@use '../base/variables' as v;

.cargando {

    .cargandobarra {
        border-bottom: 5px solid v.$primario;
        overflow: hidden;
        white-space: nowrap;
       
        //border-right: 0.15em solid v.$primario;
        animation: progres 3s linear;
    }
    margin: 2rem auto;    
    max-width: fit-content;

    h1 {
        text-align: center;
        border-right: 3px solid;
        width: fit-content;
        white-space: nowrap;
        overflow: hidden;
        margin: 0 auto;
        animation: typing 2s steps(16), blink .5s step-end infinite;
    }
}

@keyframes progres {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

@keyframes typing {
    0% {
        width: 0;
    }

    100% {
        width: 100%;
    }
}

@keyframes blink {
    50% {
        border-color: transparent
    }
}