@use 'variables' as v;
@use './mixins' as m;


.boton-principal{
    @include m.boton(v.$grisoscuro,v.$primario, block);
    white-space: nowrap;
}
.boton-secundario{
    @include m.boton(v.$primario,v.$grisoscuro, block);
    white-space: nowrap;
    width: fit-content;
}
.boton-eliminar{
    @include m.boton(v.$rojo,v.$gris, block);
    white-space: nowrap;
    width: fit-content;
}
