@use '../base/variables' as v;



.allmensajes{
    max-width: 800px;
    max-height: 800px;
    overflow-y: scroll;
    scroll-behavior: smooth;
    margin: 3rem auto;
}
.contenedorMensaje{
    
    
    background-color: v.$gris;
    margin: 2rem auto;
    .nombre{
        color: v.$grisoscuro;
        font-weight: v.$bold;
        &:hover{
            cursor: pointer;
            color: darken($color: v.$primario, $amount: 10%);
        }
    }
}