@use '../base/variables' as v;
@use '../base/mixins' as m;


.contenedor-mensaje{
    .detalle-mensjae{
        border: solid 1px v.$grisoscuro;
        @include m.borderRadius;
        padding: 2rem;
    }

    .infoContacto{
        display: block;
        
        @include m.desktop{
            display: flex;
            justify-content: space-between;
            align-items: center;
            align-content: center;
            gap: 2rem;
        }
       
        span{
            font-weight: v.$bold;
            display: block;
        }
    }
   
}