@use 'variables' as v;
@use 'mixins' as m;

.errores{
    background-color: v.$rojo;
    padding: 1rem;
    font-weight: v.$bold;
    font-size: 1.4rem;
    color: v.$blanco;
    text-align: center;
    text-transform: uppercase;
    margin: 2rem 0;
}
.error{
    border: solid 1px v.$rojo;
}