@use 'variables' as v;
@use 'mixins' as m;

.contenedor {
    
    width: 95%;
    padding: 5rem 0;
    margin: 3rem auto;
    @include m.desktop{
        max-width: 1200px;
    }
}

h1, h2 {
    font-family: v.$fuente_principal;
    font-weight: v.$bold;
    text-align: center;
}
h2::after,
.trabajos::after {
    content: url('/static/img/puntitos.png');
    display: flex;
    flex-direction: column;
    align-items: center;
}
h3{
    font-family: v.$fuente-texto;
}
.sombra {
    -webkit-box-shadow: 0px 5px 15px -3px rgba(120, 120, 120, 0.58);
    -moz-box-shadow: 0px 5px 15px -3px rgba(120, 120, 120, 0.58);
    box-shadow: 0px 5px 15px -3px rgba(120, 120, 120, 0.58);
    padding: 2rem;
    border-radius: 1rem;
}
.margin-2rem{
    margin: 2rem auto;
}
.visto{
    background-color: v.$gris;
}
.no-visto{
    border: solid 2px v.$primario;
}
.selected{
    background-color: lighten($color: v.$grisoscuro, $amount: 15%)
}
.overlay {
    position: fixed;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.651);
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    padding: 3rem;
    place-items: center; //elinea al centro
    z-index: 999;
      .contImg{
        height: 90%;
        max-height: 600px;
        width: 100%;
        z-index: 999;
        @supports(object-fit: contain) {
            img {
                display: block;
                margin: 0 auto;
                width: auto;
                height: 100%;
                object-fit: cover;
                border: 3px solid v.$gris;
            }
        }
      }
   
   
}
.none{
    display: none;
}