@use '../base/variables' as v;
@use '../base/mixins' as m;


.navbar{
    max-width: 100%;
    background-color: v.$grisoscuro;    
    .cont-nav {
           width: 100%;
            .logoNav{
                background-image: url('/static/img/bore.png');
                height: 70px;
                width: 100%;
                flex: 0 1 20rem;
                margin: 0 auto;
                background-position: center center;
                background-repeat: no-repeat;
                background-size: contain;
                
            }
            .enlacesNav{
                display: block;
                margin: 0 auto;
                text-align: center;
                color: v.$blanco;
                font-size: 1.8rem; 
               li {
                padding: 1rem;
                @include m.borderRadius;
             
                &:hover{
                    background-color: v.$primario;
                    cursor: pointer;
                    transition: background-color 0.5s linear;
                }
               }
            }
        }
    
}

.LinkAdmin::after {
    background-image: url('/static/img/perfil.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    display: inline-block;
    width: 20px; 
    height: 20px;
    content:"";
       
    
}

@include m.desktop{
    .navbar{ 
        .cont-nav {
                display: flex;
                align-items: center;
                align-content: flex-start;
                justify-content: left;
                max-width: 1200px;
                height: 80px;
                margin: 0 auto;
                .enlacesNav{
                    display: flex;
                    justify-content: center;
                    color: v.$blanco;
                    gap: 2rem;
                    font-size: 1.8rem; 
                    flex: 0.8;
                   li {
                    padding: 1rem;
                    @include m.borderRadius;
                    
                   
                    &:hover{
                        background-color: v.$primario;
                        cursor: pointer;
                        transition: background-color 0.5s linear;
                    }
                   }
                }
            }
        
    }
}
