@use '../base/variables' as v;
@use '../base/mixins' as m;


.info {
    color: v.$grisoscuro;
    h3{
        text-align: center;
    }
}

.info .imagen-info {
    flex-direction: column;
}

.info .imagen-info img {
    border-radius: 1rem;
    max-width: 10rem;
}
.info-ema{
    
    span {
        font-size: 1.8rem;
        color: v.$grisoscuro;
        font-weight: v.$bold;
    }
}
.img__ema {
    width: 23rem;
    height: 100%;
    
    
}

@include m.desktop {
    .info .imagen-info {
        padding: 2rem;
        flex-direction: row;
        align-items: center;
    }
    .info .imagen-info img {
        max-width: 25rem;
        /* filter: grayscale(25);*/
    }
    .info .info-ema {
        padding: 2rem;
        flex-direction: column;
    }
   
}
.contacto{
    display: flex;
    flex-direction: column;
    align-content: center;
    .contButton{
        width: fit-content;
        display: block;
        margin: 0 auto;
    }
       
  
}
